/**
 * Site header
 */
.site-title {
  @include relative-font-size(1.625);
  font-weight: 300;
  line-height: $base-line-height * $base-font-size * 2.25;
  letter-spacing: -1px;
  margin-bottom: 0;
  float: left;

  &,
  &:visited {
    color: $city-light;
  }
}

/**
 * Site footer
 */
.site-footer {
  border-top: 1px solid $grey-color-light;
  padding: $spacing-unit 0;
  margin-top: 200px;
}

.footer-heading {
  @include relative-font-size(1.125);
  margin-bottom: $spacing-unit / 2;
}

.contact-list,
.social-media-list {
  list-style: none;
  margin-left: 0;
}

.footer-col-wrapper {
  @include relative-font-size(0.9375);
  color: $grey-color;
  margin-left: -$spacing-unit / 2;
  @extend %clearfix;
}

.footer-col {
  float: left;
  margin-bottom: $spacing-unit / 2;
  padding-left: $spacing-unit / 2;
}

.footer-col-1 {
  width: -webkit-calc(35% - (#{$spacing-unit} / 2));
  width:         calc(35% - (#{$spacing-unit} / 2));
}

.footer-col-2 {
  width: -webkit-calc(20% - (#{$spacing-unit} / 2));
  width:         calc(20% - (#{$spacing-unit} / 2));
}

.footer-col-3 {
  width: -webkit-calc(45% - (#{$spacing-unit} / 2));
  width:         calc(45% - (#{$spacing-unit} / 2));
}

@include media-query($on-laptop) {
  .footer-col-1,
  .footer-col-2 {
    width: -webkit-calc(50% - (#{$spacing-unit} / 2));
    width:         calc(50% - (#{$spacing-unit} / 2));
  }

  .footer-col-3 {
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width:         calc(100% - (#{$spacing-unit} / 2));
  }
}

@include media-query($on-palm) {
  .footer-col {
    float: none;
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width:         calc(100% - (#{$spacing-unit} / 2));
  }
}



/**
 * Page content
 */
.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  margin-top: 20px;
  @include relative-font-size(1.75);
}

.post-list {
  margin-left: 0;
  margin-top: 15px;
  list-style: none;
  display: flex;
  border-radius: 20px;
}

.post-meta {
  font-size: 17px;
  color: $white;
}

// JAVA
.post-link {
  display: block;
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  border: 2px solid $java-color;
  border-radius: 20px;
  background-color: $java-color;
  width: 100%;
  @include relative-font-size(1.25);
  h3 {
    color: $white;
  }
}

.post-link:hover {
  text-decoration: none;
  background-color: $soothing-breeze;
  border: 2px solid $soothing-breeze;
  .post-meta {
    color: black;
  }
  > h3 { 
    color: black;
  }
}


